import navCss from "../../nav/nav.module.scss";

const Input = ({input, placeholder, type}) => {
    return <>
        <div className={navCss.inputWrapper}><input className={navCss.input} {...input}
                                                    placeholder={placeholder} type={type}/>
            <button className={navCss.button}/>
            <hr className={navCss.underlineSearching}/>
        </div>
    </>
}

export default Input