import "./app.module.scss"

import {Route, Switch} from "react-router-dom";

import Nav from "./components/nav/nav-container";
import Main from "./components/pages/main/main";
import Privacy from "./components/pages/privacy/privacy";
import Faq from "./components/pages/faq/faq";
import Terms from "./components/pages/terms/terms";
import Delivery from "./components/pages/delivery/delivery";
import Footer from "./components/footer/footer-container";
import Error404 from "./components/pages/error404/error404-container";

function App({routes}) {
    return <Switch>
        <Route exact path={routes.main} render={() => <><Nav/><Main/><Footer/></>}/>
        <Route exact path={routes.privacy} render={() => <><Nav/><Privacy/><Footer/></>}/>
        <Route exact path={routes.faq} render={() => <><Nav/><Faq/><Footer/></>}/>
        <Route exact path={routes.terms} render={() => <><Nav/><Terms/><Footer/></>}/>
        <Route exact path={routes.delivery} render={() => <><Nav/><Delivery/><Footer/></>}/>
        <Route render={() => <Error404/>}/>
    </Switch>
}

export default App;
