import {useEffect} from "react";
import {useLocation} from "react-router-dom";

const ScrollToTopPagesContainer = () => {
    const {pathname} = useLocation();
    useEffect(() => {
        window.scrollTo({top: 0}) || document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].scrollTo({top: 0})
    }, [pathname]);
    return null;
}

export default ScrollToTopPagesContainer;
