import navCss from "../nav.module.scss";
import appCss from "../../../app.module.scss";

const NavTemplateCatalogSearchAcc = ({
                                         handleClick,
                                         handleClickClose,
                                         text,
                                         innerHTML = null,
                                         cla = null, $elForOpen, $menu, $elForClose,
                                         children
                                     }) => {
    return <li className={cla}>
        <div onClick={handleClick} ref={$elForOpen}>{text ? text : innerHTML}</div>
        <div className={navCss.seacatacc} ref={$menu}>
            <div className={appCss.container}>
                <div className={appCss.content}>
                    <div className={navCss.seacatacccrossWrapper}>
                        <div onClick={handleClickClose} ref={$elForClose}>X</div>
                    </div>
                    {children}
                </div>
            </div>
        </div>
    </li>
}

export default (NavTemplateCatalogSearchAcc);
