import Nav from "./nav";
import navCss from "./nav.module.scss"
import mainCss from "../pages/main/main.module.scss"
import {connect} from "react-redux";
import {useEffect, useState} from "react";
import {useLocation} from "react-router-dom";

const NavContainer = ({routes, scrollTop, catalogSearchAccMenuOpen}) => {
    const [classesForNav, setClassesForNav] = useState([]);
    const {pathname} = useLocation();

    //Сброс классов меню
    const resetClassNames = () => {
        setClassesForNav([]);
    }

    const changeNavToDropDownMenu = () => {
        if (catalogSearchAccMenuOpen) {
            resetClassNames();
        } else {
            setClassesForNav([navCss.active])
        }
    }

    const setStyleNav = () => {
        if (pathname.slice(pathname.lastIndexOf(routes.main)) === routes.main) {
            const navHeight = document.getElementsByClassName(`${navCss.sizeBoth}`)[0]?.clientHeight;
            const ceramicWrapperOffsetTop = document.getElementsByClassName(`${mainCss.ceramicWrapper}`)[0]?.offsetTop;
            const difference = ceramicWrapperOffsetTop - navHeight;
            if (scrollTop >= difference) {
                changeNavToDropDownMenu();
            } else {
                resetClassNames();
            }
        } else {
            changeNavToDropDownMenu();
        }
    }

    useEffect(() => {
        setStyleNav();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [scrollTop, catalogSearchAccMenuOpen, pathname]);

    const handleSubmitSearchingForm = (data) => {

    }

    return (
        <Nav handleSubmitSearchingForm={handleSubmitSearchingForm} routes={routes}
             classesForNav={" " + classesForNav.join(" ")}/>
    )
}

const mapStateToProps = (state) => {
    return {
        routes: state.app.routes,
        scrollTop: state.app.scrollTop,
        catalogSearchAccMenuOpen: state.app.catalogSearchAccMenuOpen
    }
}

export default connect(mapStateToProps)(NavContainer);
