import Preloader from "./preloader";
import {connect} from "react-redux";
import {useEffect, useState} from "react";

import preloaderCss from "./preloader.module.scss";

const PreloaderContainer = ({children, delay}) => {
    const [classesForPreloader, setClassesForPreloader] = useState([preloaderCss.preloader, preloaderCss.active]);

    const handleOffPreloader = () => {
        setClassesForPreloader([preloaderCss.preloader]);
    }

    useEffect(() => {
        setTimeout(() => {
            handleOffPreloader();
        }, delay)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <Preloader classesForPreloader={classesForPreloader.join(" ")}>
            {children}
        </Preloader>
    )
}

const mapStateToProps = (state) => {
    return {
        delay: state.app.delayStart
    }
}

export default connect(mapStateToProps)(PreloaderContainer);
