import faq from "./faq.module.scss"
import appCss from "../../../app.module.scss";

const Faq = () => {
    return <section className={faq.faq}>
        <div className={appCss.container}>
            <div className={appCss.content}><h2 className={faq.title}>Часто задаваемые вопросы</h2>
                <ol className={faq.list}>
                    <li className={faq.questF}>Куда может быть организована доставка моего заказа?
                        <ol>
                            <li>В один из магазинов Bamboo, либо по указанному вами домашнему или рабочему адресу, при
                                этом это не должен быть абонементный ящик. Возможна доставка и в Пункты выдачи заказов,
                                как в классический пункт, где выдача заказа происходит сотрудником пункта, так и в
                                Постамат - автоматизированный оранжевый терминал.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questS}>Может ли страна доставки отличаться от страны, в которой
                        осуществлен заказ?
                        <ol>
                            <li>Нет. Доставка осуществляется в пределах страны, в которой был осуществлен заказ.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questT}>Может ли адрес доставки отличаться
                        от адреса плательщика?
                        <ol>
                            <li>Да. Делая заказ, вы можете указать адрес доставки отличный от адреса, который вы
                                использовали при регистрации.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questFo}>Я получу тот же товар, который вижу на фотографии?
                        <ol>
                            <li>Да. В Bamboo.ru мы прилагаем все возможные усилия для того, чтобы показать Вам все
                                детали товаров (цвета, текстуру и т.д.) такими, какими они являются в реальности.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questFo}>Товары, в отношении которых повляется сообщение "нет в наличии" будут
                        пополняться?
                        <ol>
                            <li>Да. Товары, которые заканчиваются, пополняются нами в кратчайшие сроки. Вы также можете
                                оставить запрос на странице интересующего Вас товара, и мы направим Вам электронное
                                письмо, когда данный товар, интересующего Вас цвета и размера, будет доступен.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questFo}>Могу ли я отказаться от определенного товара в моём заказе?
                        <ol>
                            <li>Вы всегда можете удалить товар из Вашей корзины, до тех пор, пока Вы не закончите
                                полностью процесс покупки.
                            </li>
                        </ol>
                    </li>
                    <li className={faq.questFo}>Какие способы оплаты я могу использовать для осуществления покупки?
                        <ol>
                            <li>Вы можете использовать следующие способы оплаты: банковские карты Visa, MasterCard, или
                                наличный расчёт.
                            </li>
                        </ol>
                    </li>
                </ol>
            </div>
        </div>
    </section>
}

export default Faq;
