import footerCss from "../../footer/footer.module.scss";

import {email, required} from "../validators";

import {Field, reduxForm} from "redux-form";
import Input from "./input";

const MailingForm = ({handleSubmit}) => {
    return <form onSubmit={handleSubmit}>
        <div className={footerCss.mailingForm}><p
            className={footerCss.headMailing}>Рассылка</p>
            <Field type={"text"} name={"mailing"} placeholder={"E-mail"}
                   component={Input} validate={[required, email]}/>
            <p className={footerCss.textMailing}>Подпишитесь на нашу
                рассылку специальных
                предложений и получите скидку
                10% <br/> на
                следующий заказ.</p>
        </div>
    </form>
}

export default reduxForm({form: "mailing"})(MailingForm)