import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    routes: {
        main: "/",
        faq: "/faq",
        terms: "/terms",
        delivery: "/delivery",
        privacy: "/privacy",
    },
    catalogSearchAccMenuOpen: false,
    scrollTop: 0,
    delayStart: 1500
}

const appSlice = createSlice({
    initialState,
    name: "appSlice",
    reducers: {
        openCatalogSearchAccMenu: (state) => {
            state.catalogSearchAccMenuOpen = true;
        },
        closeCatalogSearchAccMenu: (state) => {
            state.catalogSearchAccMenuOpen = false;
        },
        setScrollTop: (state, action) => {
            state.scrollTop = action.payload;
        },
    }
})

const {actions, reducer} = appSlice;

export const {
    openCatalogSearchAccMenu,
    closeCatalogSearchAccMenu,
    setScrollTop
} = actions;

export default reducer;