import navCss from "./nav.module.scss";
import appCss from "../../app.module.scss";

import logo from "../../assets/images/nav/Logo.svg"
import acc from "../../assets/images/nav/account.svg"
import burgmenu from "../../assets/images/nav/burgermenu.svg"
import search from "../../assets/images/nav/search.svg"

import {Link} from "react-router-dom";
import NavTemplateCatalogSearchAcc from "./navTemplateCatalogSearchAcc/nav-template-catalog-search-acc-container";
import SearchingForm from "../forms/search/searching-form";

const Nav = ({routes, classesForNav}) => {
    return <nav className={navCss.sizeBoth + classesForNav}>
        <div className={appCss.container}>
            <ul className={appCss.content + " " + navCss.col3 + " " + navCss.nav + classesForNav}>
                <div className={navCss.leftSideNav}>
                    <NavTemplateCatalogSearchAcc text={"каталог"}/>
                    <NavTemplateCatalogSearchAcc text={"поиск"}>
                        <div className={navCss.dropDownCenter}><SearchingForm/>
                        </div>
                    </NavTemplateCatalogSearchAcc>
                </div>
                <div className={navCss.midNav}>
                    <li><Link to={routes.main}><img src={logo} alt={"logo"}/></Link></li>
                </div>
                <div className={navCss.rightSideNav}>
                    <NavTemplateCatalogSearchAcc innerHTML={<>
                        <div className={navCss.tabletAcc}>аккаунт</div>
                        <div className={navCss.mobileAcc + classesForNav}><img src={acc} alt={"acc"}/></div>
                    </>}/>
                    <li className={navCss.tabletTrash}>
                        <div>корзина</div>
                    </li>
                    <NavTemplateCatalogSearchAcc cla={navCss.mobileSearchLI} innerHTML={<>
                        <div className={navCss.mobileSearch + classesForNav}><img src={search} alt={"search"}/></div>
                    </>}>
                        <div className={navCss.dropDownCenter}><SearchingForm/>
                        </div>
                    </NavTemplateCatalogSearchAcc>
                    <li>
                        <div className={navCss.busketWrapper}>
                            <div className={navCss.busketOuter + classesForNav}>
                                <div className={navCss.busketInner}>0
                                </div>
                            </div>
                        </div>
                    </li>
                    <li className={navCss.mobileBurgMenu + classesForNav}><img src={burgmenu} alt={"burgmenu"}/></li>
                </div>
            </ul>
        </div>
    </nav>
}

export default Nav;
