import {useEffect, useRef} from "react";
import NavTemplateCatalogSearchAcc from "./nav-template-catalog-search-acc";
import {connect} from "react-redux";
import {closeCatalogSearchAccMenu, openCatalogSearchAccMenu} from "../../../redux/app/app-slice";

const NavTemplateCatalogSearchAccContainer = ({
                                                  text,
                                                  innerHTML = null,
                                                  duration = 300,
                                                  cla = null,
                                                  children,
                                                  closeCatalogSearchAccMenu,
                                                  openCatalogSearchAccMenu
                                              }) => {
    const $elForOpen = useRef();
    const $elForClose = useRef();
    const $menu = useRef();

    const closeWithoutAnim = () => {
        $elForClose.current.style.cssText = "visibility: hidden;"
        $elForOpen.current.style.cssText = ""
        $menu.current.style.cssText = "transform: translateY(-100%);"
    }

    useEffect(() => {
        window.addEventListener('resize', closeWithoutAnim);
        return () => {
            window.removeEventListener('resize', closeWithoutAnim);
        };
    }, []);


    const defaultStyle = "pointer-events: none; cursor: default; position: relative;";

    const handleClick = () => {
        $elForOpen.current.style.cssText = defaultStyle;
        const animation = $menu.current.animate([{transform: "translateY(0)"}], {duration: duration});
        animation.addEventListener('finish', openFinish);
    }

    const openFinish = () => {
        openCatalogSearchAccMenu();
        $elForOpen.current.style.cssText = defaultStyle + " z-index: 2;"
        $elForClose.current.style.cssText = "visibility: visible;"
        $menu.current.style.cssText = "transform: translateY(0);"
    }

    const closeFinish = () => {
        $elForOpen.current.style.cssText = ""
        $menu.current.style.cssText = "transform: translateY(-100%);"
    }

    const handleClickClose = () => {
        closeCatalogSearchAccMenu();
        $elForOpen.current.style.cssText = "pointer-events: none;"
        $elForClose.current.style.cssText = "visibility: hidden;"
        const animation = $menu.current.animate([{transform: "translateY(-100%)"}], {duration: duration});
        animation.addEventListener('finish', closeFinish);
    }

    return <NavTemplateCatalogSearchAcc text={text} innerHTML={innerHTML} cla={cla} handleClick={handleClick}
                                        handleClickClose={handleClickClose} $elForOpen={$elForOpen}
                                        $elForClose={$elForClose} $menu={$menu} children={children}/>
}

const mapDispatchToProps = {
    closeCatalogSearchAccMenu,
    openCatalogSearchAccMenu
}

export default connect(null, mapDispatchToProps)(NavTemplateCatalogSearchAccContainer);
