import navCss from "../../nav/nav.module.scss";

import {Field, reduxForm} from "redux-form";
import Input from "../search/input";

const SearchingForm = ({handleSubmit}) => {
    return <form onSubmit={handleSubmit}>
        <div className={navCss.searchingForm}>
            <Field type={"text"} name={"searching"} placeholder={"Поиск товара"}
                   component={Input}/>
        </div>
    </form>
}

export default reduxForm({form: "searching"})(SearchingForm)