import error404 from "./error404.module.scss";
import cat from "../../../assets/images/sections/404/cat.png"
import {Link} from "react-router-dom";

const Error404 = () => {
    return <section className={error404.sec}>
        <div className={error404.container}>
            <div className={error404.content}>
                <div className={error404.cat}><img src={cat} alt={"cat"}/></div>
                <div className={error404.innerContent}>
                    <div className={error404.errorTextDig}>404</div>
                    <div className={error404.errorText}>Упс, что-то пошло не так... (((</div>
                    <Link to={"/"}>
                        <div className={error404.buttonWrapper}>
                            <div>На главную</div>
                            <button className={error404.button}/>
                            <hr/>
                        </div>
                    </Link>
                </div>
            </div>
        </div>
    </section>
}

export default Error404;
