import appCss from "../../app.module.scss";
import footerCss from "./footer.module.scss"
import facebook from "../../assets/images/footer/formMailing/Facebook.svg"
import vk from "../../assets/images/footer/formMailing/VK.svg"
import instagram from "../../assets/images/footer/formMailing/Instagram.svg"

import MailingForm from "../forms/mailing/mailing-form"

import {Link} from "react-router-dom";

const Footer = ({handleClickToTop, handleSubmitMailingForm, routes}) => {
    return (
        <footer>
            <div className={footerCss.top + " " + appCss.container}>
                <div className={appCss.content + " " + footerCss.col2L1R1R}>
                    <div className={footerCss.mailingWrapper}><MailingForm onSubmit={handleSubmitMailingForm}/>
                        <ul className={footerCss.social}>
                            <li><a href={"https://www.facebook.com/groups/897305667536684"}><img src={facebook}
                                                                                                 alt={"facebook"}/></a>
                            </li>
                            <li><a href={"https://www.instagram.com/bam.b00k/"}><img src={instagram} alt={"instagram"}/></a>
                            </li>
                            <li><a href={"https://vk.com/pipsen"}><img src={vk} alt={"vk"}/></a></li>
                        </ul>
                    </div>
                    <div className={footerCss.shopWrapper}>
                        <table className={footerCss.table}>
                            <thead>
                            <tr>
                                <th>Магазин</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td>
                                    <div>Каталог</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Бестселлер</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Чай</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Ароматизаторы</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Керамика</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Кухня</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Ванна</div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div>Товары для дома</div>
                                </td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className={footerCss.supportWrapper}>
                        <table className={footerCss.table}>
                            <thead>
                            <tr>
                                <th>Поддержка</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <td>
                                    <div><Link to={routes.faq}>FAQ</Link></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div><Link to={routes.delivery}>Доставка</Link></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div><Link to={routes.terms}>Условия</Link></div>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <div><Link to={routes.privacy}>Конфеденциальность</Link></div>
                                </td>
                            </tr>
                            <tr className={footerCss.noSelect}>
                                <td>&nbsp;</td>
                            </tr>
                            <tr className={footerCss.noSelect}>
                                <td>&nbsp;</td>
                            </tr>
                            <tr className={footerCss.noSelect}>
                                <td>&nbsp;</td>
                            </tr>
                            <tr className={footerCss.noSelect}>
                                <td>&nbsp;</td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div className={footerCss.under + " " + appCss.container}>
                <div className={appCss.content + " " + footerCss.col3}>
                    <div onClick={handleClickToTop} className={footerCss.toTop}>
                        <button className={footerCss.button}/>
                    </div>
                    <div className={footerCss.lic}>© 2022 bamboo | Develop by Misha | Design by Sedoy</div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;
