import privacy from "./privacy.module.scss"
import appCss from "../../../app.module.scss";

const Privacy = () => {
    return <section className={privacy.privacy}>
        <div className={appCss.container}>
            <div className={appCss.content}><h2 className={privacy.title}>Политика конфиденциальности</h2>
                <ol className={privacy.list}>
                    <li className={privacy.generalProvisions}>Общие положения
                        <ol>
                            <li>Использование сайта Bamboo Пользователем означает согласие с настоящей Политикой
                                конфиденциальности и условиями обработки персональных данных Пользователя.
                            </li>
                            <li>В случае несогласия с условиями Политики конфиденциальности Пользователь должен
                                прекратить
                                использование сайта Bamboo.
                            </li>
                            <li>Настоящая Политика конфиденциальности применяется к сайту Bamboo. сайт не контролирует и
                                не
                                несет ответственность за сайты третьих лиц, на которые Пользователь может перейти по
                                ссылкам,
                                доступным на сайте Bamboo.
                            </li>
                            <li>Администрация не проверяет достоверность персональных данных, предоставляемых
                                Пользователем.
                            </li>
                        </ol>
                    </li>
                    <li className={privacy.subjectOfPrivacyPolicy}>Предмет политики конфиденциальности
                        <ol>
                            <li>Настоящая Политика конфиденциальности устанавливает обязательства Администрации по
                                неразглашению
                                и обеспечению режима защиты конфиденциальности персональных данных, которые Пользователь
                                предоставляет по запросу Администрации при регистрации на сайте Bamboo или при подписке
                                на
                                информационную e-mail рассылку.
                            </li>
                            <li>Персональные данные, разрешённые к обработке в рамках настоящей Политики
                                конфиденциальности,
                                предоставляются Пользователем путём заполнения форм на сайте Bamboo и включают в себя
                                следующую
                                информацию:
                                <ol>
                                    <li>фамилию, имя, отчество Пользователя;</li>
                                    <li>контактный телефон Пользователя;</li>
                                    <li>адрес электронной почты (e-mail);</li>
                                    <li>место жительство Пользователя;</li>
                                    <li>индекс.</li>
                                </ol>
                            </li>
                            <li>Сайт защищает Данные, которые автоматически передаются при посещении страниц:
                                <ul>
                                    <li>IP адрес;</li>
                                    <li>информация из cookies;</li>
                                    <li>информация о браузере;</li>
                                    <li>время доступа;</li>
                                    <li>реферер (адрес предыдущей страницы).</li>
                                </ul>
                                <ol>
                                    <li>Отключение cookies может повлечь невозможность доступа к частям сайта ,
                                        требующим
                                        авторизации.
                                    </li>
                                    <li>сайт осуществляет сбор статистики об IP-адресах своих посетителей. Данная
                                        информация
                                        используется с целью предотвращения, выявления и решения технических проблем.
                                    </li>
                                    <li>Любая иная персональная информация неоговоренная выше (история посещения,
                                        используемые
                                        браузеры, операционные системы и т.д.) подлежит надежному хранению и
                                        нераспространению,
                                        за исключением случаев, предусмотренных в п.п. 5.2. настоящей Политики
                                        конфиденциальности.
                                    </li>
                                </ol>
                            </li>
                        </ol>
                    </li>
                    <li className={privacy.purposesOfCollectingUserIsPersonalInformation}>Цели сбора персональной
                        информации пользователя
                        <ol>
                            <li>Персональные данные Пользователя Администрация может использовать в целях:
                                <ol>
                                    <li>Идентификации Пользователя, зарегистрированного на сайте Bamboo для его
                                        дальнейшей
                                        авторизации.
                                    </li>
                                    <li>Предоставления Пользователю доступа к персонализированным данным сайта Bamboo.
                                    </li>
                                    <li>Установления с Пользователем обратной связи, включая направление уведомлений,
                                        запросов,
                                        касающихся использования сайта Bamboo, обработки запросов и заявок от
                                        Пользователя.
                                    </li>
                                    <li>Определения места нахождения Пользователя для обеспечения безопасности,
                                        предотвращения
                                        мошенничества.
                                    </li>
                                    <li>Подтверждения достоверности и полноты персональных данных, предоставленных
                                        Пользователем.
                                    </li>
                                    <li>Создания учетной записи для использования частей сайта Bamboo, если Пользователь
                                        дал
                                        согласие на создание учетной записи.
                                    </li>
                                    <li>Уведомления Пользователя по электронной почте.</li>
                                    <li>Предоставления Пользователю эффективной технической поддержки при возникновении
                                        проблем,
                                        связанных с использованием сайта Bamboo.
                                    </li>
                                    <li>Предоставления Пользователю с его согласия специальных предложений, новостной
                                        рассылки и
                                        иных сведений от имени сайта Bamboo.
                                    </li>
                                </ol>
                            </li>
                        </ol>
                    </li>
                    <li className={privacy.additionalConditions}>Дополнительные условия
                        <ol>
                            <li>Администрация вправе вносить изменения в настоящую Политику конфиденциальности без
                                согласия
                                Пользователя.
                            </li>
                            <li>Новая Политика конфиденциальности вступает в силу с момента ее размещения на сайте
                                Bamboo, если
                                иное не предусмотрено новой редакцией Политики конфиденциальности.
                            </li>
                            <li>Все предложения или вопросы касательно настоящей Политики конфиденциальности следует
                                сообщать по
                                адресу: bamboo@gmail.com
                            </li>
                            <li>Действующая Политика конфиденциальности размещена на странице по адресу
                                https://bamboo.ru/privacy
                            </li>
                        </ol>
                        <ul>
                            <li>Обновлено: 06 Августа 2021 года
                            </li>
                            <li>г. Москва, Дизайн Самохина Н.Ю.</li>
                        </ul>
                    </li>
                </ol>
            </div>
        </div>
    </section>
}

export default Privacy;
