import delivery from "./delivery.module.scss"
import appCss from "../../../app.module.scss";

const Delivery = () => {
    return <section className={delivery.delivery}>
        <div className={appCss.container}>
            <div className={appCss.content}><h2 className={delivery.title}>Доставка и Возврат товара</h2>
                <ol className={delivery.list}>
                    <li className={delivery.deliveryT}>Доставка
                        <ol>
                            <li>Мы предлагаем бесплатную внутреннюю доставку по всем заказам от 5000 рублей.
                            </li>
                            <li>Мы используем курьеров и НЕ отправляем посылки в сейфы и почтовые ящики.
                            </li>
                        </ol>
                    </li>
                    <li className={delivery.returnOfTheProduct}>Возврат товара
                        <ol>
                            <li>К сожалению, мы не предлагаем бесплатные возвраты, поэтому, пожалуйста, выбирайте
                                тщательно.
                                Вы можете отправить свой заказ обратно за свой счет после того, как вы отправили нам по
                                электронной почте: bamboo@gmail.com.
                            </li>
                            <li>Если ваш товар заказан, вы не сможете вернуть его, если продукт не был признан
                                неисправным.
                            </li>
                            <li>Если ваш товар поврежден вам нужно будет отправить изображение по адресу
                                bamboo@gmail.com - ущерб будет оценен командой поставщика услуг.
                            </li>
                            <li>Если вы хотите обменять свой товар, вам, возможно, придется оплатить дополнительную
                                стоимость доставки, чтобы отправить свой новый продукт. Для получения дополнительной
                                информации, пожалуйста, напишите bamboo@gmail.com.
                            </li>
                            <li>Если продукты используются не по назначению, мы не несем ответственности за любой
                                причиненный ущерб.
                            </li>
                            <li>Пожалуйста, прочтите инструкции по уходу за предметами, особенно за окрашенными и
                                набивными подушками.
                            </li>
                            <li>Если вы не соблюдаете инструкции по уходу, мы не несем
                                ответственности за любой причиненный ущерб.
                            </li>
                        </ol>
                    </li>
                </ol>
            </div>
        </div>
    </section>
}

export default Delivery;
