import Footer from "./footer";
import {connect} from "react-redux";

const FooterContainer = ({routes}) => {
    const handleClickToTop = () => {
        window.scrollTo({
            behavior: "smooth",
            top: 0
        }) || document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].scrollTo({behavior: "smooth", top: 0})
    }

    const handleSubmitMailingForm = (data) => {

    }

    return <Footer handleClickToTop={handleClickToTop} handleSubmitMailingForm={handleSubmitMailingForm}
                   routes={routes}/>
}

const mapStateToProps = (state) => {
    return {
        routes: state.app.routes
    }
}

export default connect(mapStateToProps)(FooterContainer);
