import {connect} from "react-redux";
import App from "./app";
import {useEffect} from "react";
import {setScrollTop} from "./redux/app/app-slice";
import {useLocation} from "react-router-dom";
import {getClearPathName} from "./redux/help-functions";

function AppContainer({routes, setScrollTop}) {
    const handleScrollBar = () => {
        if (window.outerWidth > 1023)
            setScrollTop(document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].scrollTop)
    };

    const handleScrollWin = () => {
        if (window.outerWidth < 1024)
            setScrollTop(window.scrollY)
    };

    const handleResize = () => {
        if (window.outerWidth < 1024) {
            setScrollTop(window.scrollY);
        } else {
            setScrollTop(document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].scrollTop);
        }
    }

    useEffect(() => {
        document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].addEventListener("scroll", handleScrollBar);
        window.addEventListener("scroll", handleScrollWin);
        window.addEventListener("resize", handleResize);
        return () => {
            document.getElementsByClassName("ScrollbarsCustom-Scroller")[0].removeEventListener("scroll", handleScrollBar);
            window.removeEventListener("scroll", handleScrollWin);
            window.removeEventListener("resize", handleResize);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    //Убираем солнышко на неизвестных путях (т.е. на 404)
    const {pathname} = useLocation();
    const lastPathName = getClearPathName(pathname);

    useEffect(() => {
        if (Object.values(routes).every((route) => getClearPathName(route) !== lastPathName)) {
            document.body.style.background = "none";
        } else {
            document.body.style.background = "";
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [lastPathName]);

    return <App routes={routes}/>
}

const mapStateToProps = (state) => {
    return {
        routes: state.app.routes
    }
}

const mapDispatchToProps = {
    setScrollTop,
}

export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);
