import footerCss from "../../footer/footer.module.scss";

const Input = ({input, placeholder, type, meta: {touched, error}}) => {
    const hasError = touched && error;
    return <>
        <div className={footerCss.inputWrapper}><input className={footerCss.input} {...input}
                                                       placeholder={placeholder} type={type}/>
            <button className={footerCss.button}/>
            <hr className={footerCss.underlineMailing}/>
        </div>
        {hasError && <div className={footerCss.error}>{error}</div>}
    </>
}

export default Input